import { Fragment, useState } from 'react'
import { isObjEmpty, selectThemeColors } from '@utils'
import { useForm, Controller } from 'react-hook-form'
import Repeater from '@components/repeater'
import { CardBody, Input, Col, Label, Row, Button, Form, FormGroup} from 'reactstrap'
import { Plus, X  } from 'react-feather'
import classnames from 'classnames'
import Select from 'react-select'

const AddQuestions = ({ stepper }) => {
  const { errors, register, handleSubmit, trigger } = useForm()

  const onSubmit = () => {
    trigger()
    if (isObjEmpty(errors)) {
      stepper.next()
    }
  }
  const i = []
  const langOptions = [
    { value: '1', label: 'English' },
    { value: '2', label: 'Arabic' }
  ]

  const {  control } = useForm({
    // resolver: yupResolver(QuizDetailsSchema)
  })

  const [count, setCount] = useState(0)
  const [counts] = useState(1)
  const increaseCount = () => {
    // if (count === 5) return
    setCount(count + 1)
  }

  return (
    <Fragment>
      <div className="content-header">
        <h5 className="mb-0">Questions</h5>
        <small>Enter Questions.</small>
      </div>
      <div>
        <Form onSubmit={handleSubmit(onSubmit)}>
              <Row>
              <CardBody>
            <Form key={i}>
              <Row className='justify-content-between align-items-center'>
                  <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`question-${i}`}>Quiz Question</Label>
                    <Input   type="text"
                    id={`question-${i}`}
                    name={`question-${i}`}
                    placeholder="Who is Lionel Messi?"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`question-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                </Col>
                <Col md={4}>
                  <br/>
                <div className="content-header">
        <h5 className="mb-0">Answers</h5>
        <small>Enter Answers.</small>
      </div>
      <br/>
      <Col/>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <br/>
                  <br/>
                  <br/>
                  <br/>
                  <FormGroup>
                    <Label className="form-label" for={`answer1-${i}`}>Quiz Answer 1</Label>
                    <Input    type="text"
                    name={`answer1-${i}`}
                    id={`answer1-${i}`}
                    placeholder="Cricketer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer1-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                <br/>
                  <br/>
                  <br/>
                  <br/>
                </Col>
                <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`answer2-${i}`}>Quiz Answer 2</Label>
                    <Input  type="text"
                    name={`answer2-${i}`}
                    id={`answer2-${i}`}
                    placeholder="Footballer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer2-${i}`]
                    })}/>
                  </FormGroup>
                </Col>
                <Col md={2}>
                </Col>
                <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`answer3-${i}`}>Quiz Answer 3</Label>
                    <Input  type="text"
                    name={`answer3-${i}`}
                    id={`answer3-${i}`}
                    placeholder="VolleyBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer3-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                </Col>
                <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`answer4-${i}`}>Quiz Answer 4</Label>
                    <Input type="text"
                    name={`answer4-${i}`}
                    id={`answer4-${i}`}
                    placeholder="BasketBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer4-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                </Col>
                <Col sm={12}>
                  <hr />
                </Col>
                
              </Row>
            </Form>
      </CardBody>
              </Row>
      </Form>
      </div>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Repeater count={count}>
          {(i) => (
            <li key={i} className="list-unstyled">
              <Row>
              <CardBody>
        <Repeater count={counts}>
          {i => (
            <Form key={i}>
              <Row className='justify-content-between align-items-center'>
                  <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`question-${i}`}>Quiz Question</Label>
                    <Input   type="text"
                    id={`question-${i}`}
                    name={`question-${i}`}
                    placeholder="Who is Lionel Messi?"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`question-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                  <Button.Ripple color='danger' className='text-nowrap px-1' onClick={() => {}} outline>
                    <X size={14} className='mr-50' />
                    <span>Delete</span>
                  </Button.Ripple>
                </Col>
                <Col md={4}>
                  <br/>
                <div className="content-header">
        <h5 className="mb-0">Answers</h5>
        <small>Enter Answers.</small>
      </div>
      <br/>
      <Col/>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <br/>
                  <br/>
                  <br/>
                  <br/>
                  <FormGroup>
                    <Label className="form-label" for={`answer1-${i}`}>Quiz Answer 1</Label>
                    <Input    type="text"
                    name={`answer1-${i}`}
                    id={`answer1-${i}`}
                    placeholder="Cricketer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer1-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                <br/>
                  <br/>
                  <br/>
                  <br/>
                  <Button.Ripple color='danger' className='text-nowrap px-1' onClick={() => {}} outline>
                    <X size={14} className='mr-50' />
                    <span>Delete</span>
                  </Button.Ripple>
                </Col>
                <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`answer2-${i}`}>Quiz Answer 2</Label>
                    <Input  type="text"
                    name={`answer2-${i}`}
                    id={`answer2-${i}`}
                    placeholder="Footballer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer2-${i}`]
                    })}/>
                  </FormGroup>
                </Col>
                <Col md={2}>
                  <Button.Ripple color='danger' className='text-nowrap px-1' onClick={() => {}} outline>
                    <X size={14} className='mr-50' />
                    <span>Delete</span>
                  </Button.Ripple>
                </Col>
                <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`answer3-${i}`}>Quiz Answer 3</Label>
                    <Input  type="text"
                    name={`answer3-${i}`}
                    id={`answer3-${i}`}
                    placeholder="VolleyBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer3-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                  <Button.Ripple color='danger' className='text-nowrap px-1' onClick={() => {}} outline>
                    <X size={14} className='mr-50' />
                    <span>Delete</span>
                  </Button.Ripple>
                </Col>
                <Col md={4}>
                  <FormGroup >
            <Label className="form-label" for="quiz_lang">
              Quiz lang
            </Label>
            <Controller
              isClearable
              as={Select}
              id="quiz_lang"
              control={control}
              name="quiz_lang"
              rules={{ required: true }}
              options={langOptions}
              className={classnames('react-select', {
                'is-invalid': errors['quiz_lang']
              })}
              classNamePrefix="select"
              theme={selectThemeColors}
            />
          </FormGroup>
                  </Col>
                <Col md={4}>
                  <FormGroup>
                    <Label className="form-label" for={`answer4-${i}`}>Quiz Answer 4</Label>
                    <Input type="text"
                    name={`answer4-${i}`}
                    id={`answer4-${i}`}
                    placeholder="BasketBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer4-${i}`]
                    })} />
                  </FormGroup>
                </Col>
                <Col md={2}>
                  <Button.Ripple color='danger' className='text-nowrap px-1' onClick={() => {}} outline>
                    <X size={14} className='mr-50' />
                    <span>Delete</span>
                  </Button.Ripple>
                </Col>
                <Col sm={12}>
                  <hr />
                </Col>
                
              </Row>
            </Form>
          )}
        </Repeater>
      </CardBody>
              </Row>
            </li>
          )}
        </Repeater>
        <div className="d-flex justify-content-between">
          {count <= 30 && (
            <Button.Ripple
              className="btn-icon"
              color="primary"
              onClick={increaseCount}
            >
              <Plus size={14} />
              <span className="align-middle ml-25">Add New Question</span>
            </Button.Ripple>
          )}
        </div>
      </Form>
    </Fragment>
  )
}

export default AddQuestions


